/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  text-align: end;
}

.border-radius {
  border-radius: 0.5rem;
}

.boton-azul {
  background-color: #00303f;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 0.5rem;
  transition: background-color 0.25s ease;
  border: none;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.301);
}
.boton-azul:hover {
  background-color: #004359;
  cursor: pointer;
}

.boton-azul-block {
  background-color: #00303f;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0.5rem;
  transition: background-color 0.25s ease;
  border: none;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.301);
}
.boton-azul-block:hover {
  background-color: #004359;
  cursor: pointer;
}

.boton-verde {
  background-color: #609700;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 0.5rem;
  transition: background-color 0.25s ease;
  border: none;
}
.boton-verde:hover {
  background-color: #70b100;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: rgb(156, 51, 51);
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0.5rem;
  transition: background-color 0.25s ease;
  border: none;
}
.boton-rojo-block:hover {
  background-color: #af3939;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #609700;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 0.5rem;
  transition: background-color 0.25s ease;
  border: none;
}
.boton-verde-block:hover {
  background-color: #70b100;
  cursor: pointer;
}

.dark {
  background-color: #00090c;
}
.dark .logo {
  color: white;
}
.dark .logo span {
  font-weight: bold;
  color: #82c2d6;
}
.dark p {
  color: #FFFFFF;
}
.dark .boton {
  color: #FFFFFF;
}
.dark a, .dark h1, .dark h2, .dark h3, .dark h4, .dark label, .dark th, .dark td {
  color: #82c2d6;
}
.dark .anuncio {
  background-color: #00303f;
  border: 1px solid #00303f;
}
.dark .anuncio .boton-azul-block {
  background-color: #82c2d6;
  color: #00303f;
}
.dark .anuncio .boton-azul-block:hover {
  background-color: #5cafc9;
}
.dark .anuncio .precio {
  color: #609700;
}
.dark .anuncio .iconos-caracteristicas img {
  filter: invert(100%);
}
.dark .resumen-propiedad img {
  filter: invert(100%);
}
.dark .icono img {
  filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(245deg) brightness(100%) contrast(104%);
}
.dark .texto-nosotros blockquote {
  color: #82c2d6;
}
.dark .formulario {
  background-color: #00303f;
}
.dark .formulario input:not([type=submit]),
.dark .formulario textarea,
.dark .formulario select {
  background-color: #a8d5e3;
}
.dark .formulario fieldset {
  border: 1px solid #a8d5e3;
}
.dark .formulario fieldset legend {
  color: #a8d5e3;
}

.footer {
  background-color: #00303f;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .contenido-footer {
  padding-top: 2.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.footer .navegacion a {
  width: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.inicio {
  background-image: url(../img/header.jpg);
}

.header {
  background-color: #00303f;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  padding: 0 0 3rem 0;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  max-width: 60rem;
  font-size: 2.5rem;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    font-size: 4rem;
    padding: 0;
  }
}

.logo {
  color: white;
}

.logo span {
  font-weight: bold;
  color: #82c2d6;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    flex-direction: row;
  }
}
.derecha .dark-mode {
  filter: invert(100%);
  width: 3rem;
  margin: 1rem 0;
  transition: scale 0.25s ease;
}
@media (min-width: 768px) {
  .derecha .dark-mode {
    margin: 0 3rem 0 0;
  }
}
.derecha .dark-mode:hover {
  cursor: pointer;
  scale: 1.1;
}

.barra {
  padding: 0 0 1rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-shadow: 2px 2px 10px black;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.25s ease, opacity 0.25s linear, height 0.25s ease;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    gap: 2rem;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 1.8rem;
  display: block;
  text-align: center;
  transition: transform 0.25s ease;
}
.navegacion a:hover {
  transform: translateX(-5px);
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icono {
  text-align: center;
}
.icono img {
  height: 10rem;
}
.icono h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #a8d5e3;
  border: 1px solid #5cafc9;
  border-radius: 0.5rem;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.205);
  margin-bottom: 1rem;
}
.anuncio:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .anuncio {
    margin-bottom: 0;
  }
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3, .anuncio p {
  margin: 0;
}
.anuncio img {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}

.precio {
  color: #609700;
  font-weight: 700;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.no-webp .imagen-contactar {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contactar {
  background-image: url(../img/encuentra.webp);
}

.imagen-contactar {
  background-position: center center;
  background-size: auto;
  padding: 10rem 1rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contactar h2 {
  font-size: 4rem;
  font-weight: 900;
  color: #FFFFFF;
}
.imagen-contactar p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
.entrada-blog img {
  border-radius: 0.5rem;
}

.informacion-meta span {
  color: #82c2d6;
}

.texto-entrada a {
  color: #000000;
  display: block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .texto-entrada a {
    margin-top: 0;
  }
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #609700;
  margin-top: 1rem;
}

.testimonial {
  background-color: #609700;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}
.testimonial p {
  text-align: right;
  color: #FFFFFF;
  margin-right: 2rem;
}

.formulario {
  margin-top: 2rem;
  background-color: #cfe7ef;
  border-radius: 1rem;
  padding: 1rem;
}
.formulario p {
  font-size: 1.4rem;
  color: #00303f;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #00303f;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #82c2d6;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario fieldset {
  border: 1px solid #00303f;
  border-radius: 1rem;
}
.formulario .forma-contacto {
  max-width: 20rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenido-nosotros img {
  border-radius: 0.5rem;
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.alerta {
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  color: rgb(156, 51, 51);
  background-color: rgba(255, 0, 0, 0.164);
}
.alerta.exito {
  background-color: #609700;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #609700;
}
table.propiedades thead th {
  color: #FFFFFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}